@import url(https://fonts.googleapis.com/css2?family=Baloo+2&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
body{overflow-x:hidden;width:100vw;height:100vh;background-color:#0a021d !important;background-image:radial-gradient(circle at top right, rgba(121,68,154,0.13)),radial-gradient(circle at 20% 80%, rgba(41,196,255,0.13), transparent)}canvas{position:fixed;width:100vw;height:100vh}a{position:absolute;bottom:2vmin;right:2vmin;color:rgba(255,255,255,0.2);text-decoration:none}a:hover{color:#fff}

#projetos {
  height: 100vh;
  width: 100vw;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
}

@media (max-width: 770px) {
  #projetos {
    height: 120vh;
    margin-left: 15vh;
  }
}

@media (max-width: 500px) {
  #projetos {
    margin-left: 5vh;
  }
}
@media (max-width: 430px) {
  #projetos {
    margin-left: 0vh;
  }
}
#projetos h1 {
  text-align: center;
  font-size: 3rem;
  font-weight: 200;
  margin-top: 100px;
  margin-bottom: 50px;
  color: #fff;
  width: 100%;
  padding: 20px;
    }

.container body{
  display:-webkit-flex;
  display:flex;
  margin:0;
  padding:0;
  min-height: 100vh;
  background: #04069C;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.container{
  width: 100vw;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap;
  
}

.container .card-projetos{
  position: relative;
}

.container .card-projetos .face{
  width:300px;
  height: 200px;
  transition:.4s;
  
}

.container .card-projetos .face.face1{
  position: relative;
  background: #18151f;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content:center;
          align-content:center;
  -webkit-align-items: center;
          align-items: center;
  z-index: 1;
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
}

.container .card-projetos:hover .face.face1{
  -webkit-transform: translateY(0);
          transform: translateY(0);
  box-shadow:
    inset 0 0 60px whitesmoke,
    inset 20px 0 80px #f0f,
    inset -20px 0 80px #0ff,
    inset 20px 0 300px #f0f,
    inset -20px 0 300px #0ff,
    0 0 50px #fff,
    -10px 0 80px #f0f,
    10px 0 80px #0ff;
}


.container .card-projetos .face.face1 .content-projetos{
  opacity: .2;
  transition:  0.5s;
  text-align: center;
}

.container .card-projetos:hover .face.face1 .content-projetos{
  opacity: 1;
}

.container .card-projetos .face.face1 .content-projetos i{
  font-size: 3em;
  color: #E8BF0C;
  display: inline-block;
}

.container .card-projetos .face.face1 .content-projetos h3{
  font-size: 1em;
  font-family:  'Audiowide', cursive;
  color: #fffffff8;
  text-align: center;
}

.container .card-projetos .face.face1 .content-projetos a{
   transition: .5s;
}

.container .card-projetos .face.face2{
   position: relative;
   background: #2c253b62;
   display: -webkit-flex;
   display: flex;
   -webkit-align-items: center;
           align-items: center;
   -webkit-justify-content: center;
           justify-content: center;
   padding: 20px;
  box-sizing: border-box;
  box-shadow: 0 20px 50px rgba(0,0,0,.8);
  -webkit-transform: translateY(-100px);
          transform: translateY(-100px);
}

.container .card-projetos:hover .face.face2{
    -webkit-transform: translateY(0);
            transform: translateY(0);


}

.container .card-projetos .face.face2 .content-projetos p, a{
  font-family: 'DM Sans', sans-serif;
  font-size: 10pt;
  margin: 0 ;
  padding: 0;
  color:#ffffff; 

}

.container .card-projetos .face.face2 .content-projetos a{
  text-decoration:none;
  color: #f3f3f3;
  box-sizing: border-box;
  /* outline : 1px dashed #0C0FE8; */
  padding: 10px;
  margin: 15px 0 0;
  display: inline-block;
}

.container .card-projetos .face.face2 .content-projetos a:hover{
  /* background: red ; */
  color: #5d0e8bf8; 
  box-shadow: inset 0px 0px 10px rgba(0,0,0,0.5);
}

